<template>
  <div class="my-videos container mb-5">
    <h1 class="mb-5">MyVideos</h1>
    <VideoDetail v-if="isVideoSelected" />
    <li class="list-group-item video-item d-flex justify-content-between" v-for="(myVideo, idx) in myVideos" :key="idx">
      <img class="me-3" :src="`${myVideo.snippet.thumbnails.default.url}`" alt="video-thumbnail" @click="onVideoClick(myVideo)">
      <div class="media-body d-flex flex-column justify-content-center" @click="onVideoClick(myVideo)">
        <span>
        {{ myVideo.snippet.title }}
        </span>
      </div>
      <button @click="deleteMyVideo" class="btn">X</button>
    </li>
    
  </div>
</template>

<script>
import VideoDetail from '../components/VideoDetail.vue'
import { mapActions } from 'vuex'

export default {
  name: 'MyVideos',
  components: {
    VideoDetail
  },
  methods: {
    ...mapActions(['deleteMyVideo', 'onVideoClick'])
  },
  computed: {
    myVideos() {
      return this.$store.state.myVideos
    },
    isVideoSelected() {
      return this.$store.getters.isVideoSelected
    },
  }
}
</script>

<style scoped>
.btn:hover {
  background-color: rgb(88, 81, 138);
}

.video-item {
  display: flex;
  cursor: pointer;
}
.video-item:hover {
  background-color:rgb(230, 255, 247);
}
</style>